.comparator-container {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  
  .drop-zone {
    flex: 1;
    border: 2px dashed #ccc;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    background-color: #f8f8f8;
  }
  
  .dark .drop-zone {
    border-color: #555;
    background-color: #2a2a2a;
  }
  
  .drop-instructions {
    text-align: center;
    color: #666;
    padding: 20px;
  }
  
  .dark .drop-instructions {
    color: #aaa;
  }
  
  .drop-instructions p {
    margin: 0;
    font-size: 1.2em;
  }
  
  .image-container {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    touch-action: none;
  }
  
  .image-container img {
    position: absolute;
    transform-origin: 0 0;
    max-width: none;
    will-change: transform;
    top: 0;
    left: 0;
  }
  
  .controls {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    align-items: center;
    padding: 10px;
    background: #f5f5f5;
    border-radius: 8px;
    margin-top: 10px;
  }
  
  .dark .controls {
    background: #333;
  }
  
  button {
    padding: 8px 12px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.2s;
    background-color: #4CAF50;
    color: white;
  }
  
  .dark button {
    background-color: #2E7D32;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  .dark button:hover {
    background-color: #1B5E20;
  }
  
  button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
  .dark button:disabled {
    background-color: #555;
  }
  
  .image-info, .zoom-info {
    padding: 8px 12px;
    background-color: #e0e0e0;
    border-radius: 4px;
    font-size: 14px;
  }
  
  .dark .image-info,
  .dark .zoom-info {
    background-color: #444;
    color: #eee;
  }
  
  .lock-status {
    font-style: italic;
    color: #666;
    padding: 8px 12px;
  }
  
  .dark .lock-status {
    color: #aaa;
  }
  
  button[style*="background: #f44336"]:hover {
    background-color: #d32f2f !important;
  }
  
  button[style*="background: #2196F3"]:hover {
    background-color: #0b7dda !important;
  }
  
  @media (max-width: 768px) {
    .controls {
      flex-direction: column;
      align-items: flex-start;
    }
    
    button {
      width: 100%;
      margin-bottom: 5px;
    }
  }